import React, { useState, useEffect } from "react";
import { auth, db, storage } from "../../lib/firebase";
import { doc, getDoc, setDoc } from "firebase/firestore";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { signInWithPopup, GoogleAuthProvider } from "firebase/auth";

const Profile = () => {
  const [user, setUser] = useState(null);
  const [profileData, setProfileData] = useState({
    displayName: "",
    email: "",
    phone: "",
    bio: "",
    profilePic: "",
    resume: ""
  });

  const handleGoogleSignIn = async () => {
    const provider = new GoogleAuthProvider();
    try {
      const result = await signInWithPopup(auth, provider);
      const user = result.user;
      setUser(user);
      fetchUserData(user.uid);
    } catch (error) {
      console.error("Error during Google Sign-In:", error);
    }
  };

  const fetchUserData = async (uid) => {
    const docRef = doc(db, "users", uid);
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      setProfileData(docSnap.data());
    }
  };

  const handleFileUpload = async (e, type) => {
    const file = e.target.files[0];
    if (!file) return;

    const fileRef = ref(storage, `users/${user.uid}/${type}`);
    await uploadBytes(fileRef, file);
    const fileURL = await getDownloadURL(fileRef);

    setProfileData((prev) => ({ ...prev, [type]: fileURL }));
    await setDoc(doc(db, "users", user.uid), { ...profileData, [type]: fileURL });
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setProfileData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    await setDoc(doc(db, "users", user.uid), profileData);
  };

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-xl shadow-md">
      {!user ? (
        <button
          onClick={handleGoogleSignIn}
          className="bg-blue-500 text-white px-4 py-2 rounded-md"
        >
          Sign in with Google
        </button>
      ) : (
        <div>
          <div className="flex items-center space-x-4">
            <img
              src={profileData.profilePic || "https://via.placeholder.com/100"}
              alt="Profile"
              className="w-24 h-24 rounded-full object-cover"
            />
            <input type="file" onChange={(e) => handleFileUpload(e, "profilePic")} />
          </div>
          <input
            type="text"
            name="displayName"
            value={profileData.displayName}
            onChange={handleInputChange}
            placeholder="Full Name"
            className="border p-2 w-full mt-4"
          />
          <input
            type="email"
            name="email"
            value={profileData.email}
            onChange={handleInputChange}
            placeholder="Email"
            className="border p-2 w-full mt-4"
            disabled
          />
          <input
            type="text"
            name="phone"
            value={profileData.phone}
            onChange={handleInputChange}
            placeholder="Phone"
            className="border p-2 w-full mt-4"
          />
          <textarea
            name="bio"
            value={profileData.bio}
            onChange={handleInputChange}
            placeholder="Bio"
            className="border p-2 w-full mt-4"
          ></textarea>
          <div className="mt-4">
            <label>Resume:</label>
            <input type="file" onChange={(e) => handleFileUpload(e, "resume")} />
            {profileData.resume && (
              <a href={profileData.resume} target="_blank" rel="noopener noreferrer" className="text-blue-500">
                View Resume
              </a>
            )}
          </div>
          <button onClick={handleSave} className="mt-4 bg-green-500 text-white px-4 py-2 rounded-md">
            Save Profile
          </button>
        </div>
      )}
    </div>
  );
};

export default Profile;